<template xmlns:v-on="http://www.w3.org/1999/xhtml">
    <div class="header">
      <div class="content-wrapper"> <!--头部-->
        <div class="avatar"> <!--左边头像-->
            <img width="64" height="64" :src="seller.avatar">
        </div>
        <div class="content"><!--右边内容-->
          <div class="title"><!--标题-->
            <span class="brand"></span><!--图片-->
            <span class="name">{{seller.name}}</span>
          </div>
          <div class="description"><!--内容描述-->
            {{seller.description}}/{{seller.deliveryTime}}分钟送达
          </div>

          <div v-if="seller.supports" class="support"> <!--v-if防止拿到的数组对象为undifined而报错-->
            <span class="icon" :class="classMap[seller.supports[0].type]"></span><!--图片-->
            <span class="text">{{seller.supports[0].description}}</span>
          </div>
        </div>
        <div v-if="seller.supports" class="support-count" @click="showDetail">
          <span class="count">{{seller.supports.length}}个</span>
          <i class="icon-keyboard_arrow_right"></i>
        </div>
      </div>
      <div class="bulletin-wrapper" @click="showDetail">
        <span class="bulletin-title"></span><span class="bulletin-text">{{seller.bulletin}}</span>
        <i class="icon-keyboard_arrow_right c"></i>
      </div>
      <div class="background">
        <img :src="seller.avatar" width="100%" height="100%">
      </div>
      <!--详情弹出框-->
      <transition name="fade">
      <div v-show="detailShow" class="detail">
        <div class="detail-wrapper">
          <div class="detail-main clearfix">
            <h1 class="name">{{seller.name}}</h1>
            <div class="star-wrapper">
              <star :size="48" :score="seller.score"></star>
            </div>
            <!--优惠信息-->
            <div class="title2">
              <div class="line"></div>
              <div class="text2">优惠信息</div>
              <div class="line"></div>
            </div>
            <!--特价通知-->
            <ul v-if="seller.supports" class="supports">
              <li class="support-item" v-for="(item,index) in seller.supports">
                <span class="icon2" :class="classMap[seller.supports[index].type]"></span><!--图片-->
                <span class="text3">{{seller.supports[index].description}}</span><!--内容-->
              </li>
            </ul>
            <!--商家公告-->
            <div class="title2">
              <div class="line"></div>
              <div class="text2">商家公告</div>
              <div class="line"></div>
            </div>
            <!--具体信息-->
            <div class="bulletin">
              <p class="content2">{{seller.bulletin}}</p>
            </div>
          </div>
        </div>
        <!--关闭弹出框-->
        <div class="detail-close" @click="hideDetail">
          <i class="icon-close"></i>
        </div>
      </div>
      </transition>
    </div>
</template>

<script type="text/ecmascript-6">
  import star from 'components/star/star';
  export default {
      props:{
          seller:{
              type:Object
          }
      },
      created(){
       this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee'];
      },
      data() {
         return {
           detailShow:false
         };
       },
      methods: {
        showDetail(){
          this.detailShow = true;
        },
        hideDetail() {
          this.detailShow = false;
        }
    },
    components: {
      star:star
    },
  };

</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/mixin.styl";
  /*---------头部------------*/
  .header{
    position: relative;
    overflow: hidden;
    color: #fff;
    background: rgba(7, 17, 27, 0.5);
  }
    .content-wrapper{
       position: relative;
       padding: 24px 12px 18px 24px;
       font-size: 0;
     }
    .avatar{
     display: inline-block;
     vertical-align: top;
    }
    img{
     border-radius: 2px;
    }
    .content{
        display: inline-block;
        margin-left: 16px;
      }
    .title{
        margin: 2px 0 8px 0;
     }
  .brand{
    display: inline-block;
    vertical-align: top;
    width: 30px;
    height: 18px;
    bg-image('brand');
    background-size: 30px 18px;
    background-repeat: no-repeat;
  }
  .name{
    margin-left: 6px;
    font-size: 16px;
    line-height: 18px;
    font-weight: bold;
  }
  .description{
    margin-bottom: 10px;
    line-height: 12px;
    font-size: 12px;
  }

  .support
    .icon{
      display: inline-block;
      vertical-align: top;
      width: 12px;
      height: 12px;
      margin-right: 4px;
      background-size: 12px 12px;
      background-repeat: no-repeat;
    }
  .support
    .icon.decrease{
        bg-image('decrease_1')
       }
  .support
    .icon.discount{
        bg-image('discount_1')
       }
  .support
    .icon.guarantee{
          bg-image('guarantee_1')
        }
  .support
    .icon.invoice{
          bg-image('invoice_1')
        }
  .support
    .icon.special{
          bg-image('special_1')
        }

  .support
    .text{
      line-height: 12px;
      font-size: 10px;
    }

  .support-count{
    position: absolute;
    background-color: rgba(0,0,0,0.2);
    right:12px;
    bottom: 14px;
    padding:0 8px;
    line-height:24px;
    text-align: center;
    height: 24px;
    border-radius: 14px;
  }
  .count{
    vertical-align: top;
    font-size: 10px
  }
  .icon-keyboard_arrow_right{
    margin-left: 2px;
    line-height: 24px;
    font-size: 10px;
  }
  .bulletin-wrapper{
    position: relative;
    height: 28px;
    line-height: 28px;
    padding: 0 22px 0 12px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    background: rgba(7, 17, 27, 0.2);
  }
  .bulletin-title {
    display: inline-block;
    vertical-align: top;
    margin-top: 8px;
    width: 22px;
    height: 12px;
    bg-image('bulletin');
    background-size: 22px 12px;
    background-repeat: no-repeat;
  }
  .bulletin-text{
    vertical-align: top;
    margin: 0 4px;
    font-size: 10px;
  }
  .c{
    position: absolute;
    font-size: 10px;
    right: 12px;
    top: 5px;
  }
  /*背景图*/
  .background{
    position: absolute;
    top:0;
    height: 100%;
    width: 100%;
    z-index: -1;
    filter: blur(10px);
  }

/*详情弹出框*/
.detail{
  position: fixed;
  z-index: 100;
  top: 0;
  left:0;
  width: 100%;
  height:100%;
  background-color: rgba(7,17,27,0.7);
  transition:all 0.5s;
}
  /*---------------过渡动画------------------------*/
  .fade-transition{
    opacity: 1;
    background: rgba(7, 17, 27, 0.8);
  }
  .fade-enter,.fade-leave{
    opacity: 0;
    background: rgba(7, 17, 27, 0);
  }

.detail-wrapper{
  width: 100%;
  min-height: 100%;
}

.detail-main{
  width: 100%;
  margin-top: 64px;
  padding-bottom: 64px;
}

.detail-main .name{
  line-height: 16px;
  text-align: center;
  font-size: 16px;
  font-weight: 700;
  }

.star-wrapper{
  margin-top: 18px;
  padding: 2px 0;
  text-align: center;
}

.detail-close{
  position:relative;
  width: 32px;
  height: 32px;
  margin: -64px auto 0 auto;
}

  /*-----优惠信息----*/
  .title2{
    display: flex;
    width: 80%;
    margin: 28px auto 24px auto
  }
  .line{
    flex: 1;
    position: relative;
    top: -6px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  }
  .text2{
    padding: 0 12px;
    font-weight: 700;
    font-size: 14px;
  }

  /*-------------特价通知ul---------------*/
  .icon2{
    display: inline-block;
    vertical-align: top;
    width: 16px;
    height: 16px;
    margin-right: 6px;
    background-size: 16px 16px;
    background-repeat: no-repeat;
    margin-left: 36px;
    margin-bottom: 6px;
  }
  .support-item
    .decrease{
      bg-image('decrease_2')
    }
  .support-item
    .discount{
      bg-image('discount_2')
    }
  .support-item
    .guarantee{
      bg-image('guarantee_2')
    }
  .support-item
    .invoice{
     bg-image('invoice_2')
    }
  .support-item
    .special{
      bg-image('special_2')
    }

  .text3{
    font-size: 12px;
    font-weight: 200;
    line-height: 12px;
    color:rgb(255,255,255)
  }

  /*-----------商家公告的具体信息--------------*/
  .bulletin{
    width: 80%;
    margin: 0 auto
  }
  .content2{
      padding: 0 12px;
      line-height: 24px;
      font-size: 12px;
    }



</style>



